<template>
  <div class="doubleinput">


    <div class="wrapper"> 
      <div class="container">
        <div class="slider-trackP" ref="pr"></div>
        <input type="range" min="1"  v-model="minPrice" class="inp min" step="1" id="slider-11" 
        @input="slideOneP(),slideP(),sendMinDtaToParentComponent(),setindexmin()"
        :class="{zindex:indmin}"
        >
        <input type="range" min="1"  v-model="maxPrice"  class="inp max" step="1" id="slider-22" 
        @input="slideTwoP(),slideP(),sendMaxDtaToParentComponent(),setindexmax()"
        :class="{zindex:indmax}"
        >
      </div>
    </div>

    <!--    slideOneP(),slideP(),   setindexmin(),
          zindex,           
    . -     ,     ,         .  
              ,      findQtyOfPrices ()  -->
    
    

    
  

    
  
  </div>
</template>

<script>
  

  export default {
    
    name: 'Input-price',
    components: {
      


    },
    props:['carspropsprice'],
    

    data() {
      return {
        cars:this.carspropsprice,//      
        minPrice:null,//  ,    ,    findStartEndOfInputsArrOfInputs ()
        maxPrice:null,//  ,    ,    findStartEndOfInputsArrOfInputs ()
        arrOfPrices:[], //    ,   
        indmin:false,//,    z-index    
        indmax:false,//,    z-index      
                   
      }
    },
    watch:{
      
      carspropsprice (val) {//       
        this.cars = val
      },
      
      
        
        
    },

    methods:{
      sendMinDtaToParentComponent() {
        this.$emit('minpricedata',this.minPrice)//    ,   mounted(),      
      },
      sendMaxDtaToParentComponent() {
        this.$emit('maxpricedata',this.maxPrice)////    ,   mounted(),      
      },
      setindexmin() {//   z-index 
        this.indmin = true
        this.indmax = false
      },
      setindexmax() {//   z-index 
        this.indmin = false
        this.indmax = true
      },
      slideOneP() {//       ,    
        let minGap = 0
        let sliderOne = document.getElementById("slider-11")// id      
        let sliderTwo = document.getElementById("slider-22")// id      
        if (parseInt(sliderTwo.value) - parseInt(sliderOne.value) <= minGap) {
          sliderOne.value = parseInt(sliderTwo.value) - minGap
        }
      },
      slideTwoP() {//       ,    
        let minGap = 0
        let sliderOne = document.getElementById("slider-11")// id      
        let sliderTwo = document.getElementById("slider-22")// id      
        if (parseInt(sliderTwo.value) - parseInt(sliderOne.value) <= minGap) {
          sliderTwo.value = parseInt(sliderOne.value) + minGap
        }
      },
      slideP () {//       ,    
        let sliderOne = document.getElementById("slider-11")
        let sliderTwo = document.getElementById("slider-22")
        sliderOne.max = this.qtyOfPrices
        sliderTwo.max = this.qtyOfPrices
        console.log(sliderTwo.max);
        let sliderMaxValue = document.getElementById("slider-11").max
        let percent1 = (sliderOne.value/sliderMaxValue) * 100 - 5
        let percent2 = (sliderTwo.value/sliderMaxValue) * 100 - 5
        this.$refs.pr.style.background = `linear-gradient(to right, #D7D7D7 ${percent1}%, #7481FF ${percent1}%, #7481FF ${percent2}%,#D7D7D7 ${percent2}%)`  
     
      },

      findStartEndOfInputsArrOfInputs () {//        
        let a = []//    arrOfPrices,      
                  //  arrOfPrices   10 ,   = 1,   = 10.
        this.cars.forEach(el=>{
        let b = el.price
        a.push(b)
        
        })
        a = [...new Set(a)]
        a = Object.values(a)
        a.sort(function(a,b){
          return a-b
        })
        this.arrOfPrices = a
        let m = a[0]
        this.minPrice = a.indexOf(m)+1

        let d = a[a.length-1]
        this.maxPrice = a.indexOf(d)+1
      }, 
      
     
       
         
    },
   
    mounted () {//,        
        this.slideP()
        this.sendMinDtaToParentComponent()  
        this.sendMaxDtaToParentComponent() 
        this.findStartEndOfInputsArrOfInputs ()
        
     
      
    },
    
    computed:{
       qtyOfPrices () {//  ()     ,  
            let a = []//   slideP ().    ,    
                      //   computed ,   
          this.cars.forEach(el=>{
          let b = el.price
          a.push(b)
          
          })
          a = [...new Set(a)]
          a = Object.values(a)
          a.sort(function(a,b){
            return a-b
          })
          return a.length
       },
    
    },
  }
</script>


<style lang="scss" scoped>
  @import '../../assets/varmix.scss';
    
     .wrapper {
       
      
      width: auto;
      
     }

     .container {
      position: relative;
      margin-left: -24px;
        .zindex {
          z-index: 1; // ,    ,  z-index,  
        }             //          
     }

     .inp {
      width: auto;
     }
     

     input[type="range"] {
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      width: 100%;
      outline: none;
      position: absolute;
      margin: auto;
      top: 0;
      bottom: 0;
      background-color: transparent;
      pointer-events: none;
     }

      input[type="range"]::-webkit-slider-runnable-track {
        appearance: none;
        height: 5px;
      }

      input[type="range"]::-moz-range-track {
        appearance: none;
        height: 5px;
      }

      input[type="range"]::-ms-track{
        appearance: none;
        height: 5px;
      }

      input[type="range"]::-webkit-slider-thumb {
       appearance: none;
        height: 18px;
        width: 18px;
        background-color:#7481FF ;
        cursor: pointer;
        border-radius: 1px;
        margin-top: -7px;
        pointer-events: auto;
      }

      input[type="range"]::-moz-range-thumb {
        appearance: none;
        height: 18px;
        width: 18px;
        background-color:#7481FF ;
        cursor: pointer;
        border-radius: 1px;        
        pointer-events: auto;
      }

      input[type="range"]::-ms-thumb {
        appearance: none;
        height: 18px;
        width: 18px;
        background-color:#7481FF;
        cursor: pointer;
        border-radius: 1px; 
        pointer-events: auto;          
      }


     .slider-trackP {
      width: 100%;
      height: 5px;
      position: absolute;
      margin: auto;
      top: 0;
      bottom: 0;
      border-radius: 1px;
     }

     input:active::-webkit-slider-thumb {
      background-color: #090b18;
      border: 1px solid  #7481FF;
    }

     
     





    







  
</style>