<template>
  <div class="singleinput">


   
    
    <div class="wrapper">
      
      <input type="range" min="1"  step="1" id="slider"
      @input="startInput(),this.setSliderMax (),sendDataToParent()"
      v-model="singleinputmodel"
      >
      <div class="progress" ref="head"></div>
      
    </div>

   
       
    
    

    
  

    
  
  </div>
</template>

<script>
  

  export default {
    
    name: 'Single-input',
    components: {
      


    },
    props:['carspropsinprange'],
    

    data() {
      return {

        singleinputmodel:null,//,   
        arrofkilometers:[],
        cars:this.carspropsinprange,//      
        
              
      }
    },
    watch:{
      carspropsinprange (val) {//      ,  -  
        this.cars = val
      }
    },

    methods:{
      sendDataToParent() {//     ( singleinputmodel)  
        this.$emit('datafromsingleinput',this.singleinputmodel) //      
      },   

      setSliderMax () {
        let sliderOne = document.querySelector('#slider') 
        sliderOne.max = this.qtyOfKilometers //  max ,     ,  
      },

      startInput() {//            
        let sliderOne = document.querySelector('#slider')     
        let sliderMaxValue = document.querySelector('#slider').max
        let percent1 = (sliderOne.value/sliderMaxValue) * 100-4//  100   
        let percent2 = (sliderOne.value/sliderMaxValue) * 100-4
        this.$refs.head.style.background = `linear-gradient(to right, #7481FF ${percent1}%, #7481FF ${percent2}%,#D7D7D7 ${percent2}%)` // 
      }, //        
     
      findStartPositionOfInput () {//       
        let a = []//    arrofkilometers,      arrofkilometers,    
                  //    mounted()
        this.cars.forEach(el=>{
        let b = el.kilometers
        a.push(b)
        
        })
        a = [...new Set(a)]
        a = Object.values(a)
        a.sort(function(a,b){
          return a-b
        })
        this.arrofkilometers = a

        this.arrofkilometers = [... new Set(this.arrofkilometers)]
        this.singleinputmodel = this.arrofkilometers.length

      }, 
    },
    
    computed:{

      qtyOfKilometers () {//  ()      ,  
          let a = []//   slideS ().    ,    
                      //   computed ,   
          this.cars.forEach(el=>{
          let b = el.kilometers
          a.push(b)
          
          })
          a = [...new Set(a)]
          a = Object.values(a)
          a.sort(function(a,b){
            return a-b
          })
          return a.length
       },

    },
    mounted () {
      this.startInput()
      this.setSliderMax ()
      this.sendDataToParent()
    }
    
   

  }
</script>


<style lang="scss" scoped>
  @import '../../assets/varmix.scss';

    .wrapper{
      position: relative;
      width: auto;
  
    }

   
   
    input{
      appearance: none; 
      height: 6px;
      width: 100%;
      background-color: transparent;
      outline: none;
      -webkit-transition: .2s;
      transition: opacity .2s;
      cursor: pointer;
      width: 100%;

    }

    input::-webkit-slider-thumb {
      appearance: none; 
      height: 18px;
      width: 18px;
      background: #7481FF;
      cursor: pointer;
      border-radius: 1px;
      margin-top: -17px;
    }

    .progress{
      appearance: none; 
      border-radius: 1px; 
      height: 6px;
      background-color: #D7D7D7;
      margin-top: -20px;
    }

    input[type="range"]:active::-webkit-slider-thumb {
      background-color: #090b18;  
      border: 1px solid  #7481FF;
    }
 
</style>